/* Import Tailwind's base styles, components, and utilities */
@tailwind base;
@tailwind components;
@tailwind utilities;

/* Define custom styles and theme variables within a layer */
@layer base {
  :root {
    --bg-color: #121212;
    --primary-color: #FFFFFF;
    --secondary-color: #a7a7a7;
    --accent-color: #E50914; /* FMS Red */
    --card-bg: #1a1a1a;
  }

  body {
    background-color: var(--bg-color);
    color: var(--primary-color);
    font-family: 'Inter', sans-serif;
    cursor: none;
    overflow-x: hidden;
  }

  /* Custom Cursor */
  .cursor-dot, .cursor-outline {
    position: fixed;
    top: 0;
    left: 0;
    transform: translate(-50%, -50%);
    border-radius: 50%;
    pointer-events: none;
    z-index: 9999;
    transition: all 0.1s ease-out;
  }

  .cursor-dot {
    width: 8px;
    height: 8px;
    background-color: var(--accent-color);
  }

  .cursor-outline {
    width: 40px;
    height: 40px;
    border: 2px solid var(--accent-color);
    opacity: 0.5;
  }

  .cursor-dot.hover, .cursor-outline.hover {
    transform: translate(-50%, -50%) scale(1.5);
  }

  .cursor-outline.hover {
      background-color: rgba(229, 9, 20, 0.2);
      opacity: 1;
  }

  a, button {
    cursor: none;
  }
}
